// ---- navbar start ----

$test: 0;
// navbar
.navbar-container {
    max-width: 100%;
    width: 100%;
    height: 100%;
    font-size: 1.1rem;
    margin: 0 auto;

    // navbar
    .navbar-inner-container {
        max-width: 100%;
        width: 70%;
        margin: 0 auto;
        // height: auto;
        height: 55px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;

        @include media768 {
            width: 90%;
            height: auto;
        }
        @include media568 {
            width: 100%;
            justify-content: center;
        }
    }

    // navbar(ui)
    .navbar-content {
        width: 90%;
        height: 100%;
        display: flex;
        justify-content: flex-end;
        flex-wrap: wrap;
        position: relative;
        transition: all 0.3s ease-in-out;
        // overflow: hidden;

        // 568className
        @include media568 {
            position: fixed;
            width: 100vw;
            height: 100vh;
            right: -110%;
            top: 0;
            z-index: 30;
            justify-content: flex-start;
            flex-direction: column;
            align-items: center;

            // RWD | 
            li:nth-of-type(1)::after {
                content: '';
            }
            li:nth-of-type(2)::after {
                content: '';
            }
            li:nth-of-type(3)::after {
                content: '';
            }
            li:nth-of-type(4)::after {
                content: '';
            }
        }
    }

    // navbar 
    .navbar-items {
        display: flex;
        justify-items: center;
        align-items: center;
        height: 100%;
        position: relative;

        @include media568 {
            width: 100%;
            height: 60px;
            flex-direction: column;
            cursor: pointer;
            border-bottom: 2px solid #dfe6e9;
        }

        // 
        &::before {
            content: '';
            position: absolute;
            top: 90%;
            left: 50%;
            transform: translateX(-50%);
            height: 0;
            width: 0;
            border-bottom: 3px solid #00b894;
            transition: all 0.5s ease;
        }

        // 
        &:hover::before {
            width: 80%;
        }
        &:active::before {
            width: 80%;
            border-bottom: 3px solid #4a3ac2;
        }

        //  RWD
        @include media568 {
            &:hover::before {
                width: 20%;
            }
            &:active::before {
                width: 20%;
                border-bottom: 3px solid #4a3ac2;
            }
        }

        //  
        .navbar-items-text {
            padding: 25px 15px;
            transition: all 0.2s ease-in-out;
            display: flex;
            justify-content: center;
            align-items: center;

            &:hover {
                color: #00b894 !important;
            }

            &:active {
                color: #4a3ac2 !important;
            }
        }

        // 
        .product-all-items {
            padding: 0;
            transition: all 0.2s ease-in-out;
            display: flex;
            justify-content: center;
            align-items: center;
            cursor: pointer;
            width: 100%;

            &:hover {
                color: #00b894;
            }
        }
    }

    //  icon
    .product-type-icon {
        display: inline-block;
        width: 15px;
        height: 15px;
        margin: 0 0 0 5px;
    }
}

// style!important
.header-menu-open,
.header-menu-leave {
    width: 40px;
    height: 40px;
    transition: all 0.5s ease;
    cursor: pointer;

    &:hover {
        fill: #00b894 !important;
    }
    &:active {
        fill: #4a3ac2 !important;
    }
}

// 568
.header-menu-open {
    display: none;
    @include media568 {
        display: inline-block;
        position: absolute;
        right: 5%;
    }
}

// 568
.header-menu-leave {
    display: none;
    @include media568 {
        width: 55px;
        height: 55px;
        display: inline-block;
        z-index: 30;
        position: absolute;
        top: 5%;
        right: 5%;
    }

    @include media414 {
        position: absolute;
        top: 2%;
    }

    @include media375 {
        width: 35px;
        height: 35px;
    }
}

// ---- navbar end ----

// 
.showMenu {
    right: 0 !important;
}

// 
.product-type-container {
    width: 100%;
    height: 300px;
    background-color: #111414;
    // background-color: #fff;
    // background-color: #00b894;
    color: #fff;
    position: absolute;
    top: 100%; //  ()
    display: flex;
    justify-content: center;
    align-items: flex-start;
    padding: 50px 20px;
    border-bottom: 2px solid black;
    font-size: 1.1rem;
    // display: none;

    // 
    @include media568 {
        display: none !important;
    }

    // 
    .product-type-title {
        font-size: 1.5rem;
        display: flex;
        justify-content: center;
        align-items: center;

        &:hover {
            color: #4a3ac2;
        }
    }

    //  ul 
    .product-rackets-menu,
    .product-strings-menu,
    .product-balls-menu,
    .product-accessories-menu,
    .product-clothes-menu,
    .product-shoes-menu,
    ul {
        margin: 0 40px;

        @include media1140 {
            margin: 0 30px;
        }

        @include media768 {
            margin: 0 8px;
        }

        //  h3 
        h3 {
            font-size: 1.5rem;
            margin: 0 0 15px;
            &:hover {
                color: #4a3ac2;
            }
        }

        //  li 
        li {
            // font-weight: 200;
            font-size: 1rem;
            margin: 0 0 10px;

            // 
            a {
                &:hover {
                    color: #00b894;
                }
            }
        }
    }
}

