// banner  start
.home-banner-container,
.home-banner-container-skeleton {
    max-width: 100%;
    width: 100%;
    height: 400px;
    position: relative;
    margin: 0 auto;

    @include media568 {
        height: 200px;
    }
    @include media414 {
        height: 40vh;
    }

    // img  skeleton 
    .img-container-skeleton {
        width: 100%;
        height: 100%;
    }
    //  <figure><figcaption> 
    .figure-container {
        display: inline;
        position: relative;

        // 
        .figcaptionText {
            display: none;
        }
    }

    // glide.js  className  (ul)
    .glide__slides {
        width: 100%;
        height: 400px;
        margin: 0 auto;
        position: relative;

        @include media568 {
            height: 200px;
        }
        @include media414 {
            height: 40vh;
        }
    }

    // li  
    .glide__slide img,
    .glide__slide video {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }

    /*  */
    .glide__arrows {
        // 100%
        width: 100%;
        height: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: absolute;
        left: 0;
        top: 45%;

        // 
        .arrow-left,
        .arrow-right {
            width: 50px;
            height: 50px;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: 0;
            cursor: pointer;
            opacity: 0.5;
            transition: opacity 0.3s ease-in-out;

            &:hover {
                opacity: 1;
            }
            &:active {
                opacity: 1;
            }
        }
        // 
        .arrow-left {
            left: 2%;
        }
        // 
        .arrow-right {
            right: 2%;
        }
    }

    //  ()
    .glide__bullets {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        left: 0;
        top: 90%;

        // 
        .glide__bullet {
            border-radius: 0;
            margin: 1px;
            width: 40px;
            height: 10px;
            opacity: 0.5;
            // transition: opacity 0.3s ease-in-out;
            cursor: pointer;

            &:hover {
                opacity: 1;
            }

            &:active {
                opacity: 1;
            }
        }

        // button
        .glide__bullet--active {
            opacity: 0.9 !important;
        }
    }
}
